    "HELPER.ADMINHELPER.ADDPARTICIPANTSFROMLDAP" : "<h5>From Active Directory</h5>[node:addParticipantsFromLDAPtext]",
    "HELPER.ADMINHELPER.ADDPARTICIPANTSFROMLDAPTEXT" : "Go to [action:ADintegration]Active Directory Integration[/action] to specify connection settings and get groups.",
    "HELPER.ADMINHELPER.ADDPARTICIPANTSLICENSE" : "<h5>Assign a User to the Product Key</h5>[node:addParticipantsLicenseText]",
    "HELPER.ADMINHELPER.ADDPARTICIPANTSLICENSETEXT" : "(for on-premise software version). Open [action:LicenseManager]License Manager[/action], click <strong>Manage Keys</strong>, select a user to assign the key.",
    "HELPER.ADMINHELPER.ADDPARTICIPANTSTM" : "<h3>Add Users and User Groups</h3>[node:addParticipantsFromLDAP][node:addParticipantsTracker]",
    "HELPER.ADMINHELPER.ADDPARTICIPANTSTR" : "<h3>Add Users and User Groups</h3>[node:addParticipantsFromLDAP][node:addParticipantsTracker][node:addParticipantsLicense]",
    "HELPER.ADMINHELPER.ADDPARTICIPANTSTRACKER" : "<h5>Add Local Users and User Groups</h5>[node:addParticipantsTrackerText]",
    "HELPER.ADMINHELPER.ADDPARTICIPANTSTRACKERTEXT" : "Go to [action:Users]Users[/action], click <strong>Add New User</strong>. Go to [action:Usergroups]User Groups[/action], create a new group or edit membership of existing user groups.",
    "HELPER.ADMINHELPER.APPLYFORLICENSE" : "<h3>Get the Comindware Tracker License</h3>[node:applyForLicenseText]",
    "HELPER.ADMINHELPER.APPLYFORLICENSETEXT" : "Open [action: licensemanager]License Manager[/action], click <strong>Buy Product&nbsp;Key</strong>, or <strong>Get&nbsp;Free/Trial&nbsp;Key</strong>.",
    "HELPER.ADMINHELPER.CONTACTSYSADMIN" : "<h3>Contact System Administrator</h3>[node:contactSysadminText]",
    "HELPER.ADMINHELPER.CONTACTSYSADMINTEXT" : "Contact your Administrator if you need to add users, teams, groups, connect to Active Directory or corporate e-mail server.",
    "HELPER.ADMINHELPER.CREATEENVIRONMENTOWN" : "<h5>Create Your Own Work Environment</h5>[node:createEnvironmentOwnTemplate][node:createEnvironmentOwnGroup][node:createEnvironmentOwnWS]",
    "HELPER.ADMINHELPER.CREATEENVIRONMENTOWNGROUP" : "<h3>Create a Group</h3>[node:createEnvironmentOwnGroupText]",
    "HELPER.ADMINHELPER.CREATEENVIRONMENTOWNGROUPTEXT" : "Open [action:Groups]Groups[/action], click <strong>New Group</strong>. Specify general settings, add the created template to it.",
    "HELPER.ADMINHELPER.CREATEENVIRONMENTOWNTEMPLATE" : "<h3>Create a Template</h3>[node:createEnvironmentOwnTemplateText][node:createEnvironmentOwnTemplateFields][node:createEnvironmentOwnTemplateForm][node:createEnvironmentOwnTemplateWorkflow]",
    "HELPER.ADMINHELPER.CREATEENVIRONMENTOWNTEMPLATEFIELDS" : "<h5>Configure Fields</h5>[node:createEnvironmentOwnTemplateFieldsText]",
    "HELPER.ADMINHELPER.CREATEENVIRONMENTOWNTEMPLATEFIELDSTEXT" : "In a template, click <strong>Fields</strong>. Click <strong>New Field</strong>, select field type, set field properties, save the field. Then click <strong>Save Template</strong>.",
    "HELPER.ADMINHELPER.CREATEENVIRONMENTOWNTEMPLATEFORM" : "<h5>Set up the Web Form</h5>[node:createEnvironmentOwnTemplateFormText]",
    "HELPER.ADMINHELPER.CREATEENVIRONMENTOWNTEMPLATEFORMTEXT" : "In a template, click <strong>Form</strong>. Drag and drop the fields you want to add from the left part of the window into the middle part of the window. Select the form element and specify field properties in the right part of the window. Click <strong>Save</strong>.",
    "HELPER.ADMINHELPER.CREATEENVIRONMENTOWNTEMPLATETEXT" : "Open [action:Templates]Templates[/action], click <strong>New</strong>. Configure a set of fields, a web form, a workflow.",
    "HELPER.ADMINHELPER.CREATEENVIRONMENTOWNTEMPLATEWORKFLOW" : "<h5>Create a Workflow</h5>[node:createEnvironmentOwnTemplateWorkflowText]",
    "HELPER.ADMINHELPER.CREATEENVIRONMENTOWNTEMPLATEWORKFLOWTEXT" : "In a template, click <strong>Workflows</strong>, select the workflow.  Create a workflow scheme which consists of steps and transitions. Drag and drop the status from the left area to the canvas area to create a workflow step. Double click on the step to specify its settings, e.g. assignee and transition rules. Interconnect steps with transitions. Double click on a transition to specify its settings. When finished, click <strong> Save Workflow</strong>.",
    "HELPER.ADMINHELPER.CREATEENVIRONMENTOWNTM" : "<h5>Create Your Own Work Environment</h5>[node:createEnvironmentOwnWS][node:createEnvironmentOwnGroup]",
    "HELPER.ADMINHELPER.CREATEENVIRONMENTOWNWS" : "<h3>Create a Workspace</h3>[node:createEnvironmentOwnWSText]",
    "HELPER.ADMINHELPER.CREATEENVIRONMENTOWNWSTEXT" : "Open [action:Workspaces]Workspaces[/action], click <strong>New Workspace</strong>. Specify general settings, add the created group to it.",
    "HELPER.ADMINHELPER.CREATEENVIRONMENTPREDESIGNED" : "<h5>Use a Pre-Designed Work Environment</h5>[node:createEnvironmentPredesignedText]",
    "HELPER.ADMINHELPER.CREATEENVIRONMENTPREDESIGNEDTEXT" : "Open [action:Workspaces]Workspaces[/action], click <strong>Workspace Templates</strong>, select your solution.",
    "HELPER.ADMINHELPER.CREATEENVIRONMENTTM" : "<h3>Create a Work Environment</h3>[node:createWorkEnvironmentArchNote][node:createEnvironmentOwnWS][node:createEnvironmentOwnGroup]",
    "HELPER.ADMINHELPER.CREATEENVIRONMENTTR" : "<h3>Create Your Process</h3>[createWorkEnvironmentArchNote][node:createEnvironmentOwnTemplate][node:createEnvironmentOwnGroup][node:createEnvironmentOwnWS]\r\n",
    "HELPER.ADMINHELPER.CREATEWORKENVIRONMENTARCHNOTE" : "This type of configuration may also be performed by a member of Comindware Architects group.",
    "HELPER.ADMINHELPER.DESCRIPTION" : "These steps will help you quickly set up and customize your work environment.",
    "HELPER.ADMINHELPER.ENTERPRODUCTKEY" : "<h3>Enter Your Product Key</h3>[node:enterProductKeyText]",
    "HELPER.ADMINHELPER.ENTERPRODUCTKEYTEXT" : "Open [action: licensemanager]License Manager[/action], click <strong>Add Product&nbsp;Key</strong>.<br/> If you do not have a key, <a href=\"http://www.comindware.com/tracker/try/?utm_source=tracker&utm_medium=NA&utm_term=NA&utm_content=try&utm_campaign=product\" target=\"_blank\">get a free/trial product key</a> or <a href=\"http://www.comindware.com/tracker/buy/?utm_source=tracker&utm_medium=NA&utm_term=NA&utm_content=buy&utm_campaign=product\" target=\"_blank\">buy the Comindware Tracker product key</a>.<br/>",
    "HELPER.ADMINHELPER.OTHERADMINSETUP" : "<h3>Specify Optional Settings (Administration)</h3>[node:otherAdminSetupEmail][node:otherAdminSetupBackup]",
    "HELPER.ADMINHELPER.OTHERADMINSETUPBACKUP" : "<h5>Configure Backups</h5>[node:otherAdminSetupBackupText]",
    "HELPER.ADMINHELPER.OTHERADMINSETUPBACKUPTEXT" : "Go to [action: Backup]Backup Management[/action], click <strong>New Backup</strong>.",
    "HELPER.ADMINHELPER.OTHERADMINSETUPEMAIL" : "<h5>Configure Outgoing E-mail Server Settings</h5>[node:otherAdminSetupEmailText]",
    "HELPER.ADMINHELPER.OTHERADMINSETUPEMAILTEXT" : "Required to send e-mail notifications. Go to [action: Emailr]E-mail Notifications[/action], specify your e-mail server settings.",
