    "HELPER.TUTORIALS.TUTORIALHOWTOCREATEWORKFLOWFROMSCRATCH.31EMAILNOTIFICATIONS.DESCRIPTION" : "This section lets you configure e-mail notifications for workflow tasks in this App. Click here to continue.",
    "HELPER.TUTORIALS.TUTORIALHOWTOCREATEWORKFLOWFROMSCRATCH.31EMAILNOTIFICATIONS.TITLE" : "E-mail Notifications",
    "HELPER.TUTORIALS.TUTORIALHOWTOCREATEWORKFLOWFROMSCRATCH.32EVENTSANDRECIPIENTS" : "Events and Recipients",
    "HELPER.TUTORIALS.TUTORIALHOWTOCREATEWORKFLOWFROMSCRATCH.32EVENTSANDRECIPIENTS.DESCRIPTION" : "This is the list of events that trigger email notifications, and respective users who will get them. Click the pencil button to edit the list of users.",
    "HELPER.TUTORIALS.TUTORIALHOWTOCREATEWORKFLOWFROMSCRATCH.32EVENTSANDRECIPIENTS.TITLE" : "Events and Recipients",
