    "HELPER.TUTORIALS.TUTORIALHOWTOCREATEWORKFLOWFROMSCRATCH.33CLICKSAVEANDCLOSE.TITLE" : "Click \"Save & Close\"",
    "HELPER.TUTORIALS.TUTORIALHOWTOCREATEWORKFLOWFROMSCRATCH.34EXPORTSETTINGS" : "Export Settings",
    "HELPER.TUTORIALS.TUTORIALHOWTOCREATEWORKFLOWFROMSCRATCH.34EXPORTSETTINGS.DESCRIPTION" : "Every workflow task can be exported as a Microsoft Word - formatted document. Click here to customize that function.",
    "HELPER.TUTORIALS.TUTORIALHOWTOCREATEWORKFLOWFROMSCRATCH.34EXPORTSETTINGS.TITLE" : "Export Settings",
    "HELPER.TUTORIALS.TUTORIALHOWTOCREATEWORKFLOWFROMSCRATCH.35CUSTOMIZINGEXPORTFORMAT" : "Customizing the export format",
    "HELPER.TUTORIALS.TUTORIALHOWTOCREATEWORKFLOWFROMSCRATCH.35CUSTOMIZINGEXPORTFORMAT.DESCRIPTION" : "You can upload your own document format to be used for exporting data from your workflow tasks. See our Help to learn how to create such a custom export template.",
    "HELPER.TUTORIALS.TUTORIALHOWTOCREATEWORKFLOWFROMSCRATCH.35CUSTOMIZINGEXPORTFORMAT.TITLE" : "Customizing the export format",
